:root {
  --background-color: #fefefe;
  --text-color: black;
}

body {
  color: var(--text-color);
  background: #fefefe;
  font-family: Arial, Helvetica, sans-serif;
}

.heading:before, .heading:after {
  content: "";
  width: 20px;
  height: 20px;
  background: #087a22;
  border-radius: 50%;
  display: inline-block;
}

.goals {
  color: gold;
}

.main {
  padding: 20px;
}

nav a {
  color: #fff;
}

a {
  color: red;
}

.header {
  min-height: 60px;
  width: 100%;
  background: #fefefe;
  margin-bottom: 10px;
  position: fixed;
}

.navigation {
  padding: 16px;
  display: none;
}

.navigation--open {
  background: #087a22;
  display: block;
}

.navigation__link {
  color: #fff;
  margin: 0 0 16px;
  display: block;
}

.footer {
  color: #fefefe;
  justify-content: space-between;
  display: flex;
}

.top {
  background: #bbd814;
  border-radius: 12px;
}

.name {
  height: 60vh;
  color: #fefefe;
  background: linear-gradient(45deg, #020024 0%, #090979 15%, #00d4ff 100%);
  justify-content: center;
  align-items: center;
  margin-top: 40px;
  padding: 20px;
  display: flex;
}

.article, .aside {
  background: var(--background-color);
}

.heady {
  display: flex;
}

.picture {
  max-width: 100%;
  height: auto;
}

.hamburger {
  color: #fefefe;
  border: #fff;
  border-style: inherit;
  background: #087a22;
  padding: 20px;
  position: absolute;
  top: 10px;
  right: 40px;
}

.switch {
  width: 60px;
  height: 34px;
  position: absolute;
  top: 20px;
  left: 40px;
}

.darkmode {
  color: #a5a5a5;
  background: #222;
}

@media screen and (width >= 600px) {
  .main {
    grid-column-gap: 20px;
    background: #090979;
    grid-template-columns: 3fr 1fr;
    grid-template-areas: "header header"
                         "article aside"
                         "footer footer";
    display: grid;
  }

  .name {
    height: 80vh;
    grid-area: header;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .article {
    grid-area: article;
    padding: 20px;
  }

  .aside {
    grid-area: aside;
    padding: 20px;
  }

  .footer {
    grid-area: footer;
    padding: 20px;
  }

  .navigation {
    background: #087a22;
    justify-content: center;
    padding: 12px;
    display: flex;
  }

  .navigation__link {
    color: #fff;
    padding: 16px;
    display: block;
  }

  .hamburger {
    display: none;
  }
}

